<template>
    <div>
        <el-card class="box-card" style="width: 400px">
            <div slot="header" class="clearfix">
                <span>{{hr.name}}</span>
            </div>
            <div>
                <div style="display: flex; justify-content: center">
                    <img title="修改图像" :src="hr.userface" alt="" style="width: 100px;height: 100px;border-radius: 50px">
                </div>
                <div>电话号码：<el-tag>{{hr.telephone}}</el-tag></div>
                <div>手机号码：<el-tag>{{hr.phone}}</el-tag></div>
                <div>居住地址：<el-tag>{{hr.address}}</el-tag></div>
                <div>用户标签：<el-tag type="success" style="margin-right: 5px;" v-for="(r,index) in hr.roles" :key="index">{{r.nameZh}}</el-tag></div>
                <div style="display: flex; justify-content: space-around;margin-top: 10px">
                    <el-button type="primary">修改信息</el-button>
                    <el-button @click="showEditEmpView()" type="warning" style="padding: 3px" size="mini">修改密码</el-button>
                </div>
            </div>
        </el-card>
        <el-dialog :title="title" :visible.sync="dialogVisible" width="80%">
            <div>
                <el-form :model="hr" :rules="rules" ref="hrForm">
                    <el-row>
                        <el-col :span="6">
                            <el-form-item label="密码:" prop="name">
                                <el-input size="mini" style="width: 150px" v-model="hr.password" placeholder="请输新密码"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="warning" @click="doChangePassword">确 定</el-button>
        </span>
        </el-dialog>
    </div>
</template>

<script>
    import {getRequest} from "../utils/api";

    export default {
        name: "HrInfo",
        data() {
            return{
                // hr: null,
                dialogVisible: false,
                title: '',
                hr: {
                    password: ""
                }
            }
        },
        mounted() {
            this.initHr();
        },
        methods:{
            showEditEmpView() {
                console.log("xiugai")
                this.title = '修改密码';
                this.dialogVisible = true;
            },
            doChangePassword() {
                if (this.hr.id) {
                    console.log("密码888");
                    this.$refs['hrForm'].validate(valid => {
                        console.log(valid);
                        if (valid) {
                            console.log("密码2")
                            this.putRequest("/hr/password", this.hr).then(resp => {if (resp) {
                                    this.dialogVisible = false;
                                    // this.initEmps();
                                }
                            })
                        }
                    });
                } else {
                    console.log("密码错")
                    this.$refs['hrForm'].validate(valid => {
                        if (valid) {
                            this.postRequest("/hr/password", this.hr).then(resp => {
                                if (resp) {
                                    this.dialogVisible = false;
                                    // this.initEmps();
                                }
                            })
                        }
                    });
                }
            },
            initHr() {
                console.log("初始化");
                this.getRequest('/hr/info').then(resp=>{
                    if (resp) {
                        this.hr = resp;
                    }
                })
            }
        }
    }
</script>

